input::-moz-focus-inner {
  border: 0;
  padding: 0; }

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */ }

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
button,
input,
select,
textarea {
  color: #222; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

img {
  vertical-align: middle; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

textarea {
  resize: vertical; }

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0; }

/* box-sizing */
.fr-window,
.fr-window [class^="fr-"],
.fr-overlay,
.fr-overlay [class^="fr-"],
.fr-spinner,
.fr-spinner [class^="fr-"] {
  box-sizing: border-box; }

.fr-window {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  font: 13px/20px "Lucida Sans", "Lucida Sans Unicode", "Lucida Grande", Verdana, Arial, sans-serif;
  /* Chrome hack, this fixes a visual glitch when quickly toggling a video */
  transform: translateZ(0px); }

/* z-index */
.fr-overlay {
  z-index: 99998; }

.fr-window {
  z-index: 99999; }

.fr-spinner {
  z-index: 100000; }

/* overlay */
.fr-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.fr-overlay-background {
  float: left;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.9;
  filter: alpha(opacity=90); }

.fr-overlay-ui-fullclick .fr-overlay-background {
  background: #292929;
  opacity: 1;
  filter: alpha(opacity=100); }

/* mobile touch has position:absolute to allow zooming */
.fr-window.fr-mobile-touch,
.fr-overlay.fr-mobile-touch {
  position: absolute;
  overflow: visible; }

/* some properties on the window are used to toggle things
 * like margin and the fullclick mode,
 * we reset those properties after measuring them
 */
.fr-measured {
  margin: 0 !important;
  min-width: 0 !important;
  min-height: 0 !important; }

.fr-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.fr-pages {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.fr-page {
  position: absolute;
  width: 100%;
  height: 100%; }

.fr-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  text-align: center; }

.fr-hovering-clickable .fr-container {
  cursor: pointer; }

/*
   padding:
   youtube & vimeo always have buttons on the outside
   side button = 72 = 54 + (2 * 9 margin)
   close button = 48
*/
/* padding ui:inside */
.fr-ui-inside .fr-container {
  padding: 20px 20px; }

.fr-ui-inside.fr-no-sides .fr-container {
  padding: 20px; }

/* padding ui:outside */
.fr-ui-outside .fr-container {
  padding: 20px 82px; }

.fr-ui-outside.fr-no-sides .fr-container {
  padding: 20px 48px; }

/* reduce padding on smaller screens */
@media all and (max-width: 700px) and (orientation: portrait), all and (orientation: landscape) and (max-height: 700px) {
  /* padding ui:inside */
  .fr-ui-inside .fr-container {
    padding: 12px 12px; }
  .fr-ui-inside.fr-no-sides .fr-container {
    padding: 12px; }
  /* padding ui:outside */
  .fr-ui-outside .fr-container {
    padding: 12px 72px; }
  .fr-ui-outside.fr-no-sides .fr-container {
    padding: 12px 48px; } }

@media all and (max-width: 500px) and (orientation: portrait), all and (orientation: landscape) and (max-height: 500px) {
  /* padding ui:inside */
  .fr-ui-inside .fr-container {
    padding: 0px 0px; }
  .fr-ui-inside.fr-no-sides .fr-container {
    padding: 0px; }
  /* padding ui:outside */
  .fr-ui-outside .fr-container {
    padding: 0px 72px; }
  .fr-ui-outside.fr-no-sides .fr-container {
    padding: 0px 48px; } }

/* padding ui:fullclick */
.fr-ui-fullclick .fr-container {
  padding: 0; }

.fr-ui-fullclick.fr-no-sides .fr-container {
  padding: 0; }

/* video fullclick */
.fr-ui-fullclick.fr-type-video .fr-container {
  padding: 0px 62px; }

.fr-ui-fullclick.fr-no-sides.fr-type-video .fr-container {
  padding: 48px 0px; }

/* overflow-y should always have 0 padding top and bottom */
.fr-overflow-y .fr-container {
  padding-top: 0 !important;
  padding-bottom: 0 !important; }

.fr-content,
.fr-content-background {
  position: absolute;
  top: 50%;
  left: 50%;
  /* IE11 has a 1px blur bug on the edges of the caption (ui:inside)
     during animation with overflow:hidden */
  overflow: visible; }

/* img/iframe/error */
.fr-content-element {
  float: left;
  width: 100%;
  height: 100%; }

.fr-content-background {
  background: #101010; }

.fr-info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: #efefef;
  font-size: 13px;
  line-height: 20px;
  text-align: left;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text; }

.fr-info-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #000;
  line-height: 1%;
  filter: none;
  opacity: 1; }

.fr-ui-inside .fr-info-background {
  filter: alpha(opacity=80);
  opacity: 0.8;
  zoom: 1; }

/* ui:outside has a slightly lighter info background to break up from black page backgrounds */
.fr-ui-outside .fr-info-background {
  background: #0d0d0d; }

/* info at the bottom for ui:inside */
.fr-content .fr-info {
  top: auto;
  bottom: 0; }

.fr-info-padder {
  display: block;
  overflow: hidden;
  padding: 12px;
  position: relative;
  width: auto; }

.fr-caption {
  width: auto;
  display: inline; }

.fr-position {
  color: #b3b3b3;
  float: right;
  line-height: 21px;
  opacity: 0.99;
  position: relative;
  text-align: right;
  margin-left: 15px;
  white-space: nowrap; }

/* positions not within the info bar
   for ui:outside/inside */
.fr-position-outside,
.fr-position-inside {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 12px;
  width: auto;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 11px;
  line-height: 20px;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
  display: none;
  overflow: hidden;
  white-space: nowrap;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text; }

.fr-position-inside {
  border: 0; }

.fr-position-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #0d0d0d;
  filter: alpha(opacity=80);
  opacity: 0.8;
  zoom: 1;
  /* oldIE */ }

.fr-position-text {
  color: #b3b3b3; }

.fr-position-outside .fr-position-text,
.fr-position-inside .fr-position-text {
  float: left;
  position: relative;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  opacity: 1; }

/* display it for the outside ui, hide the caption inside */
.fr-ui-outside .fr-position-outside {
  display: block; }

.fr-ui-outside .fr-info .fr-position {
  display: none; }

.fr-ui-inside.fr-no-caption .fr-position-inside {
  display: block; }

/* links */
.fr-info a,
.fr-info a:hover {
  color: #ccc;
  border: 0;
  background: none;
  text-decoration: underline; }

.fr-info a:hover {
  color: #eee; }

/* no-caption */
.fr-ui-outside.fr-no-caption .fr-info {
  display: none; }

.fr-ui-inside.fr-no-caption .fr-caption {
  display: none; }

/* --- stroke */
.fr-stroke {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px; }

.fr-stroke-vertical {
  width: 1px;
  height: 100%; }

.fr-stroke-horizontal {
  padding: 0 1px; }

.fr-stroke-right {
  left: auto;
  right: 0; }

.fr-stroke-bottom {
  top: auto;
  bottom: 0; }

.fr-stroke-color {
  float: left;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.08); }

/* the stroke is brighter inside the info box */
.fr-info .fr-stroke-color {
  background: rgba(80, 80, 80, 0.3); }

/* remove the stroke when outside ui has a caption */
.fr-ui-outside.fr-has-caption .fr-content .fr-stroke-bottom {
  display: none; }

/* remove stroke and shadow when ui:fullclick */
.fr-ui-fullclick .fr-stroke {
  display: none; }

.fr-ui-fullclick .fr-content-background {
  box-shadow: none; }

/* the info box never bas a top stroke */
.fr-info .fr-stroke-top {
  display: none; }

/* < > */
.fr-side {
  position: absolute;
  top: 50%;
  width: 54px;
  height: 72px;
  margin: 0 9px;
  margin-top: -36px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  zoom: 1; }

.fr-side-previous {
  left: 0; }

.fr-side-next {
  right: 0;
  left: auto; }

.fr-side-disabled {
  cursor: default; }

.fr-side-hidden {
  display: none !important; }

.fr-side-button {
  float: left;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0; }

.fr-side-button-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #101010; }

.fr-side-button-icon {
  float: left;
  position: relative;
  height: 100%;
  width: 100%;
  zoom: 1;
  background-position: 50% 50%;
  background-repeat: no-repeat; }

/* smaller side buttons */
@media all and (max-width: 500px) and (orientation: portrait), all and (orientation: landscape) and (max-height: 414px) {
  .fr-side {
    width: 54px;
    height: 60px;
    margin: 0;
    margin-top: -30px; }
  .fr-side-button {
    width: 48px;
    height: 60px;
    margin: 0 3px; } }

/* X */
.fr-close {
  position: absolute;
  width: 48px;
  height: 48px;
  top: 0;
  right: 0;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none; }

.fr-close-background,
.fr-close-icon {
  position: absolute;
  top: 12px;
  left: 12px;
  height: 26px;
  width: 26px;
  background-position: 50% 50%;
  background-repeat: no-repeat; }

.fr-close-background {
  background-color: #000; }

/* Thumbnails */
.fr-thumbnails {
  position: absolute;
  overflow: hidden; }

.fr-thumbnails-disabled .fr-thumbnails {
  display: none !important; }

.fr-thumbnails-horizontal .fr-thumbnails {
  width: 100%;
  height: 12%;
  min-height: 74px;
  max-height: 160px;
  bottom: 0; }

.fr-thumbnails-vertical .fr-thumbnails {
  height: 100%;
  width: 10%;
  min-width: 74px;
  max-width: 160px;
  left: 0; }

.fr-thumbnails,
.fr-thumbnails * {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.fr-thumbnails-wrapper {
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%; }

.fr-thumbnails-vertical .fr-thumbnails-wrapper {
  top: 50%;
  left: 0; }

.fr-thumbnails-slider {
  position: relative;
  width: 100%;
  height: 100%;
  float: left;
  zoom: 1; }

.fr-thumbnails-slider-slide {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%; }

.fr-thumbnails-thumbs {
  float: left;
  height: 100%;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0; }

.fr-thumbnails-slide {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%; }

.fr-thumbnail-frame {
  position: absolute;
  zoom: 1;
  overflow: hidden; }

.fr-thumbnail {
  position: absolute;
  width: 30px;
  height: 100%;
  left: 50%;
  top: 50%;
  zoom: 1;
  cursor: pointer;
  margin: 0 10px; }

.fr-ltIE9 .fr-thumbnail * {
  overflow: hidden;
  /* IE6(7) */
  z-index: 1;
  zoom: 1; }

.fr-thumbnail-wrapper {
  position: relative;
  background: #161616;
  width: 100%;
  height: 100%;
  float: left;
  overflow: hidden;
  display: inline;
  /* IE6(7) */
  z-index: 0;
  /* IE8 */ }

.fr-thumbnail-overlay {
  cursor: pointer; }

.fr-thumbnail-active .fr-thumbnail-overlay {
  cursor: default; }

.fr-thumbnail-overlay,
.fr-thumbnail-overlay-background,
.fr-thumbnail-overlay-border {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-width: 0;
  overflow: hidden;
  border-style: solid;
  border-color: transparent; }

.fr-ltIE9 .fr-thumbnail-overlay-border {
  border-width: 0 !important; }

.fr-thumbnail .fr-thumbnail-image {
  position: absolute;
  filter: alpha(opacity=85);
  opacity: 0.85;
  max-width: none; }

.fr-thumbnail:hover .fr-thumbnail-image,
.fr-thumbnail-active:hover .fr-thumbnail-image {
  filter: alpha(opacity=99);
  opacity: 0.99; }

.fr-thumbnail-active .fr-thumbnail-image,
.fr-thumbnail-active:hover .fr-thumbnail-image {
  filter: alpha(opacity=35);
  opacity: 0.35; }

.fr-thumbnail-active {
  cursor: default; }

/* Thumbnails loading */
.fr-thumbnail-loading,
.fr-thumbnail-loading-background,
.fr-thumbnail-loading-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.fr-thumbnail-loading-background {
  background-color: #161616;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.8;
  position: relative;
  float: left; }

/* this element is there as an alternative to putting the loading image on the background */
.fr-thumbnail-loading-icon {
  display: none; }

/* error on thumbnail */
.fr-thumbnail-error .fr-thumbnail-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #202020; }

/* Thumbnail < > */
.fr-thumbnails-side {
  float: left;
  height: 100%;
  width: 28px;
  margin: 0 5px;
  position: relative;
  overflow: hidden; }

.fr-thumbnails-side-previous {
  margin-left: 12px; }

.fr-thumbnails-side-next {
  margin-right: 12px; }

.fr-thumbnails-vertical .fr-thumbnails-side {
  height: 28px;
  width: 100%;
  margin: 10px 0; }

.fr-thumbnails-vertical .fr-thumbnails-side-previous {
  margin-top: 20px; }

.fr-thumbnails-vertical .fr-thumbnails-side-next {
  margin-bottom: 20px; }

.fr-thumbnails-side-button {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -14px;
  margin-left: -14px;
